export default {
    data() {
        return {
            providerList: []
        }
    },
    methods:{
        //登录
        tologin(provider) {
            uni.login({
                provider: provider.id,



                success: (res) => {
                    console.log(JSON.stringify(res))
                    console.log('login success:', res);
                    // 更新保存在 store 中的登录状态
                    console.log("登录成功 保存到本地 修改当前登录状态")
                },
                fail: (err) => {
                    console.log('login fail:', err);
                }
            });
        },
        //获取登陆渠道
        getLoadingAuth(){
            uni.getProvider({
                service: 'oauth',
                success: (result) => {
                    this.providerList = result.provider.map((value) => {
                        let providerName = '';
                        let icon = ''
                        switch (value) {
                            case 'weixin':
                                providerName = '微信登录'
                                icon = 'weixin'
                                break;
                            case 'qq':
                                providerName = 'QQ登录'
                                icon = 'QQ'
                                break;
                            case 'sinaweibo':
                                providerName = '新浪微博登录'
                                icon = 'xinlangweibo'
                                break;
                            case 'xiaomi':
                                providerName = '小米登录'
                                icon = 'gengduo'
                                break;
                            case 'alipay':
                                providerName = '支付宝登录'
                                icon = 'gengduo'
                                break;
                            case 'baidu':
                                providerName = '百度登录'
                                icon = 'gengduo'
                                break;
                            case 'toutiao':
                                providerName = '头条登录'
                                icon = 'gengduo'
                                break;
                            case 'apple':
                                providerName = '苹果登录'
                                icon = 'gengduo'
                                break;
                        }
                        return {
                            name: providerName,
                            icon: icon,
                            id: value
                        }
                    });
                },
                fail: (error) => {
                    console.log('获取登录通道失败', error);
                }
            });
        }
    },
    created() {
        this.getLoadingAuth()
    }
}
